.ctype-nessa_partner img {
  display: block;
  width: 100%;
  vertical-align: middle;
  border-style: none;
  transition: all .6s ease; }

.ctype-nessa_portfolio .portfolio-filters {
  margin: 25px auto 25px auto;
  padding: 0;
  text-align: center;
  list-style: none;
  border-radius: 50px; }
  .ctype-nessa_portfolio .portfolio-filters button {
    padding: 10px 15px;
    color: #0B2341;
    font-weight: 600;
    font-size: 14px;
    line-height: 1;
    background: none;
    border: 0;
    cursor: pointer;
    transition: all .3s ease-in-out; }
    .ctype-nessa_portfolio .portfolio-filters button:focus {
      color: #DB0819;
      outline: none; }
    .ctype-nessa_portfolio .portfolio-filters button:hover {
      color: #DB0819; }
    .ctype-nessa_portfolio .portfolio-filters button:last-child {
      margin-right: 0; }
  .ctype-nessa_portfolio .portfolio-filters button.filter-active {
    color: #DB0819; }

.ctype-nessa_portfolio .portfolio-item {
  margin-bottom: 30px; }
  .ctype-nessa_portfolio .portfolio-item .portfolio-info {
    position: absolute;
    right: 30px;
    bottom: 0;
    left: 30px;
    z-index: 3;
    padding: 15px;
    background: #e9ecef;
    opacity: 0;
    transition: all ease-in-out .3s; }
    .ctype-nessa_portfolio .portfolio-item .portfolio-info h4 {
      color: #AAAAAA;
      font-weight: 600;
      font-size: 18px; }
    .ctype-nessa_portfolio .portfolio-item .portfolio-info p {
      margin-bottom: 0;
      color: #444444;
      font-size: 14px; }
    .ctype-nessa_portfolio .portfolio-item .portfolio-info .preview-link {
      position: absolute;
      top: calc(50% - 18px);
      right: 40px;
      color: #0B2341;
      font-size: 24px;
      transition: .3s; }
      .ctype-nessa_portfolio .portfolio-item .portfolio-info .preview-link:hover {
        color: #DB0819; }
    .ctype-nessa_portfolio .portfolio-item .portfolio-info .details-link {
      position: absolute;
      top: calc(50% - 18px);
      right: 10px;
      color: #0B2341;
      font-size: 24px;
      transition: .3s; }
      .ctype-nessa_portfolio .portfolio-item .portfolio-info .details-link:hover {
        color: #DB0819; }
  .ctype-nessa_portfolio .portfolio-item:hover .portfolio-info {
    bottom: 20px;
    opacity: 1; }

